import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import SpeedDial from "@mui/material/SpeedDial";

const dispatchedEvents = [];

const SrsMuiSpeedDial = function (props, ref) {
  return (
    <SpeedDial
      ariaLabel={props["aria-label"]}
      direction={props.direction}
      icon={props.icon}
      open={props.open}
      onOpen={props.onOpen}
      onClose={props.onClose}
      theme={props.muiTheme}
    >
      <slot name="default" />
    </SpeedDial>
  );
};

const styles = `
  /* Custom styles for the MuiSpeedDial component */
`;

const props = [
  {
    name: "aria-label",
    defaultValue: null,
    type: "string",
  },
  {
    name: "direction",
    defaultValue: "up",
    type: "string",
  },
  {
    name: "icon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onOpen",
    defaultValue: null,
    type: "function",
  },
  {
    name: "onClose",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-speed-dial-action"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSpeedDial),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
